@import '@outlier-spa/mine/dist/styles/_helpers.scss';

$maxWidth: 1600px;

*,
*:after,
*:before {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-rendering: optimizeLegibility;
}

.fullpage {
	height: 1250px;
	overflow: hidden;
}

html,
body {
	min-width: 320px;
	min-height: 480px;
	overflow-x: hidden;
	width: 100%;
	height: 100%;
	padding: 0 !important;
	margin: 0;
	font-size: 16px !important;
}

body {
	font-family: 'Open Sans', serif;
	// font-size: 14px;
	color: $white;
}

#root {
	width: 100%;
	height: 100%;
}

.sidenav {
	position: absolute;
	left: 0;
	z-index: 3;
	top: 0;
	width: 200px;
	height: 100%;
	color: white;
	// background-color: $darkgray1;
	@include transition;
	.header {
		text-align: center;
	}
}

.app-wrapper {
	width: 100%;
	height: 100%;
	// background-color: $gray2;

	.overlay {
		position: fixed;
		z-index: 2;
		background-color: rgba($color: #000, $alpha: 0.6);
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		@include transition;
		opacity: 0;
		pointer-events: none;
	}

	.dynamic-container {
		position: relative;
		width: 100%;
		padding-left: 200px;
		height: 100%;
		z-index: 1;
		@include transition;

		> .header {
			padding: 0 30px;
			font-size: 16px;
			position: relative;

			> img {
				height: 30px;
				position: absolute;
				left: 0;
				top: 0;
				bottom: 0;
				margin: auto;
				opacity: 0;
				pointer-events: none;
				@include transition;
			}
			> svg {
				height: 30px;
				position: absolute;
				left: 0;
				top: 0;
				bottom: 0;
				margin: auto;
				font-size: 28px;
				color: $skyblue;
				opacity: 0;
				pointer-events: none;
				@include transition;
			}

			> span {
				font-size: 14px;
				color: $softgray3;
				font-weight: normal;
			}

			.profile-nav {
				position: absolute;
				right: 30px;
				top: 0;
			}
		}

		.inner-content {
			display: flex;
			flex-direction: column;
			padding: 30px;
			min-height: calc(100% - 70px);
			max-width: $maxWidth;

			&.clean-inner {
				padding: 0;
			}
		}
	}
}

.header {
	height: 60px;
	line-height: 60px;
	font-weight: 600;
	padding: 0 15px;
	@include transition;
}

.content {
	height: calc(100% - 60px);
	overflow: auto;
}

.footer {
	width: 100%;
	height: 70px;
}

.sidenav {
	.wrapper__collapsible {
		font-size: 16px;
		// color: $softgray3;
		@include user-select(none);

		.nav-item {
			display: block;
			padding: 12px 15px 13px 15px;

			@include transition(none);
			&:active,
			&:hover {
				color: inherit;

				span {
					color: $skyblue;
				}
			}
			span {
				@include transition;
			}
			&.active span {
				color: $skyblue;
			}
		}

		&.ant-collapse > .ant-collapse-item {
			border: none;

			> .ant-collapse-header {
				color: inherit;
				padding: 12px 15px 13px 15px;
			}
			&.ant-collapse-item-active,
			&.active {
				> .ant-collapse-header {
					color: $skyblue;
				}
			}
		}

		.ant-collapse-content {
			color: inherit;
			> .ant-collapse-content-box {
				// background-color: $gray3;
				padding: 4px 10px 10px 10px;
				border-bottom: 1px solid $gray1;
			}
		}
	}

	.profile {
		display: flex;
		flex-direction: column;
		padding: 0 36px;
		line-height: 36px;

		.profile-details {
			text-align: left;
			padding: 0 16px;
			line-height: initial;

			span.role {
				font-weight: normal;
				font-size: 24px;
			}
			span.other {
				color: #cddef2;
				font-size: 22px;
			}
		}
	}
}

.inline-fields {
	display: inline-table;
	margin-right: 10px;
}

.txt-left {
	text-align: left;
}

.inline-fields:last-child {
	margin-right: 0;
}

.section-header {
	justify-content: space-between;
}

.ant-collapse-icon-position-right > .ant-collapse-item > .ant-collapse-header,
.ant-collapse-content > .ant-collapse-content-box {
	padding-left: 0 !important;
	padding-right: 0 !important;
}

.help-header {
	font-family: Open Sans;
	font-style: normal;
	font-weight: 600;
	font-size: 14px;
	line-height: 19px;
	padding-right: 10px;
	span {
		margin-left: 6px;
	}
}

.translation-y-hover:hover {
	transform: translateY(-5px);
}

@media screen and (max-width: 900px) {
	.profile-nav {
		.profile-nav__inner {
			padding-right: 20px;
		}
		.profile-nav__button {
			display: block;
		}
		.profile-nav__text {
			display: none;
		}
		.nav-option__username {
			display: block;
		}
	}
}

@media only screen and (max-width: 1200px) {
	.sidenav {
		left: -400px;

		&.active {
			left: 0;

			& + .overlay {
				opacity: 1;
				pointer-events: all;

				& + .dynamic-container {
					@include blur(2px);
				}
			}
		}
	}
	.app-wrapper {
		.dynamic-container {
			padding-left: 0;
			.header {
				text-align: center;

				img {
					left: 30px;
					opacity: 1;
					cursor: pointer;
					pointer-events: all;
				}

				svg {
					left: 36px;
					opacity: 1;
					cursor: pointer;
					pointer-events: all;
				}
			}

			.inner-content {
				text-align: left;
			}
		}
	}
}

.wp-field.error {
	border-color: $red;
}
.wp-field.error::after {
	min-width: 0 !important;
}

.trashcan {
	svg {
		font-size: 1rem;
		@include transition;
		cursor: pointer;

		&:hover {
			path {
				fill: $red;
			}
		}
	}
}
