@import '@outlier-spa/mine/dist/styles/_helpers.scss';

.table-dual-item-container {
	// background: #343941;

	/* borderPrimary */
	border: 1px solid #424851;
	border-radius: 3px;

	.table-dual-item {
		height: 56px;

		span {
			font-style: normal;
			font-weight: normal;
			font-size: 16px;
			line-height: 22px;
		}

		&:last-child {
			border-bottom: none;
		}
	}
}
