@import '@outlier-spa/mine/dist/styles/_helpers.scss';

.table-hep {
	.ant-table-cell {
		font-style: normal;
		font-weight: 600;
		font-size: 16px;
		line-height: 22px;
		padding: 14px;
	}

	.ant-table-row .ant-table-cell {
		font-style: normal;
		font-weight: normal;
		// font-size: 16px;
		// line-height: 22px;
		// padding: 16px 17px;
	}
}

.hep-page {
	height: 100%;
	.header-configuration-section,
	.header-details-section {
		.section-header {
			height: 50px !important;
			position: relative;
			font-style: normal;
			font-weight: 600;
			font-size: 16px;
			line-height: 22px;
			&::after {
				content: '';
				position: absolute;
				left: 0px;
				bottom: 0;
				height: 1px;
				width: 100%;
			}
		}
	}

	.header-configuration-section {
		height: 100%;
	}
}

.app-wrapper .dynamic-container {

	.inner-content.all-heps_page {
		padding: 0 30px 14px 30px;
		min-height: calc(100% - 70px);

		.hep-page {
			height: calc(100% - 72px);
		}
	}
}

.hep-page_holes {
	width: calc(100% - 302px);
}

.hep-page_list {
	height: calc(100% - 174px);
}

.hep-page_details {
	height: 160px;
}

.hep-page_holes,
.hep-page_list {
	flex: 1 1 0%;
    flex-direction: column;
    display: flex;
    justify-content: space-between;
}
