@import '@outlier-spa/mine/dist/styles/_helpers.scss';

.request-page {
	.upload-field {
		height: 380px;
		&:hover {
			color: $skyblue;
		}
	}
}

.request-container {
	.ant-col {
		padding: 0 !important;
	}

	.tags-non-resize .rounded-tag {
		height: 40px;
	}
}

.signature-draw-container {
	border: 1px dashed;
	border-radius: 3px;
	margin: auto;
}

//pantalla de extension de solicitud
.header-left {
	display: flex;
	flex-direction: row;
	svg {
		height: 50px;
		min-width: 50px;
	}

	.header-left-text-container {
		margin-left: 14px;
	}
	div {
		h1 {
			font-style: normal;
			font-weight: 600;
			font-size: 18px;
			line-height: 34px;
		}
		span {
			font-style: normal;
			font-weight: normal;
			font-size: 16px;
			line-height: 20px;
			// color: #becfe7;
		}
	}
}

.header-fixed-right {
	display: flex;
}

.section-trashcan-container {
	cursor: pointer;
	@include transition;
	&:hover {
		color: $red;
	}
}

.return-upload-section {
	.upload-field {
		height: 380px;

		svg {
			height: 40px;
			width: 40px;
		}

		&:hover {
			color: $skyblue;
		}
	}
}

.inner-content.return-area-page,
.inner-content.extension-request-page,
.inner-content.request-page {
	padding: 0 14px !important;
	max-width: 1440px;
	margin: auto;
}
.request-page {
	.header-left {
		padding: 19px 0 23px 0;
	}
	.header-left-principal-text {
		.rounded-tag {
			margin-left: 10px;
		}

		span:not(.rounded-tag) {
			font-style: normal;
			font-weight: 600;
			font-size: 18px;
			line-height: 34px;
		}
	}

	// collapsible
	&.especial-collapse .ant-collapse-header {
		display: flex;
		justify-content: space-between;
		position: relative;
		flex-direction: row-reverse;

		font-style: normal;
		font-weight: 600;
		font-size: 18px;
		line-height: 25px;

		padding: 8px 0;

		.separator {
			flex: 1;
			position: relative;

			&::after {
				content: '';
				position: absolute;
				height: 1px;
				width: 100%;
				top: 50%;
			}
		}
		.content {
			padding: 0 10px;
		}
		.anticon {
			padding: 8px 11px 8px 10px;
		}
	}
}

.section-header-right {
	svg path {
		@include transition;
		cursor: pointer;
	}
	svg path:hover {
		fill: $red;
	}
}

.sticky {
	position: -webkit-sticky;
	position: sticky;
	z-index: 2;
}

.top0 {
	top: 0;
}

.bottom0 {
	bottom: 0;
}

[data-tooltip].expanded__tooltip {
	&:after {
		line-height: normal;
		width: 278px;
	}
	&.no-sync::after {
		height: 77px;
		bottom: -77px;
	}
}

// ESTILO TARJETA SOLICITUD

.request-cards-container {
	cursor: pointer;
	.request-cards-header {
		font-weight: 600;
		font-size: 16px;
		line-height: 33px;
		text-align: left;
		padding: 13px 0;

		span {
			font-style: normal;
			font-weight: normal;
			line-height: 34px;
		}
	}
	.section-wrapper {
		margin-bottom: 14px;
	}

	.request-card {
		@include transition;
		&-header {
			font-weight: 400;
		}
		&-footer {
			font-weight: 400;

			.ant-tag {
				text-transform: capitalize;
			}
		}
	}
}

.hep-page {
	.ant-table,
	.ant-table-wrapper,
	.ant-spin-nested-loading,
	.ant-spin-container,
	.ant-table-ping-right:not(.ant-table-has-fix-right) .ant-table-container,
	.ant-table-container {
		height: 100% !important;
	}
	.ant-table-body {
		height: 100% !important;
		max-height: calc(100% - 54px) !important;
	}
}
