.signature-viewer {
	cursor: pointer;

	div {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100%;
		svg {
			width: 208px;
			height: 96px;
		}
	}
}

.signature-options-container {
	position: absolute;
	bottom: 10px;
	right: 10px;
	font-size: 15px;
	cursor: pointer;
	z-index: 100;
}
