@import '@outlier-spa/mine/dist/styles/_helpers.scss';

.platform-delivery-container {
	.header-fixed-left {
		padding: 21px 0;
		p {
			font-style: normal;
			font-weight: 600;
			font-size: 18px;
			line-height: 34px;
			margin-bottom: 4px;
		}
		span {
			font-style: normal;
			font-weight: normal;
			font-size: 16px;
			line-height: 20px;

			&.success {
				color: $skyblue;
			}
			&.error {
				color: $red;
			}
		}
	}

	.header-fixed-right {
		display: flex;
	}
}

.platform-card {
	@include transition;
}
