@import '@outlier-spa/mine/dist/styles/_helpers.scss';

.profile-nav {
	height: 60px;
	position: relative;
	line-height: normal;
	text-align: right;
	@include user-select(none);
	z-index: 11;
	.navigation-menu {
		height: 100%;
	}
}
.role-menu-item {
	padding: 9px 14px;
	display: flex;
	text-align: left;
	font-family: Open Sans;
	font-style: normal;
	font-weight: 600;
	font-size: 14px;
	line-height: 22px;
	.line {
		margin-left: 20px;
		width: 100%;
		position: relative;

		&:after {
			content: '';
			display: block;
			position: absolute;
			background: $gray1;
			height: 1px;
			width: calc(100% - 20px);
			left: 20px;
			top: 50%;
		}
	}
}

.role-navigation-menu-item .navigation-menu-item {
	padding: 5px 14px;
	height: auto;
	&:hover {
		background: none;
		.navigation-menu-item-roles,
		.navigation-menu-item-roles.active {
			background-color: rgba(5, 171, 232, 0.1);
			color: #fff;
		}
	}
	.navigation-menu-item-roles {
		border-radius: 3px;
		padding: 12px 10px;
		text-align: left;
	}
}

.navigation-menu-item-logout {
	font-family: Open Sans;
	font-style: normal;
	text-align: center;
	font-weight: 600;
	font-size: 14px;
	line-height: 40px;
	color: #ff6060;
}

.assay-tab {
	z-index: 1 !important;
}

.ta-center {
	text-align: center !important;
}

.navigation-menu-item-logout {
	font-family: Open Sans;
	font-style: normal;
	text-align: center;
	font-weight: 600;
	font-size: 14px;
	line-height: 40px;
	color: #ff6060;
}

@media screen and (max-width: 900px) {
	.profile-nav {
		.profile-nav__inner {
			padding-right: 20px;
		}
		.profile-nav__button {
			display: block;
		}
		.profile-nav__text {
			display: none;
		}
		.nav-option__username {
			display: block;
		}
	}
}
