@import '@outlier-spa/mine/dist/styles/_helpers.scss';

.fixed-header {
	// z-index: 5;
	width: 100%;
	height: 100px;
	max-height: 100px;
	display: flex;
	position: relative;
	justify-content: space-between;

	.fixed-header-section-left {
	}

	.fixed-header-section-center {
	}

	.fixed-header-section-right {
	}

	.fixed-header-bottom-separator {
		content: '';
		height: 1px;
		width: 100%;
		position: absolute;
		bottom: 0;
	}

	.fixed-header-top-separator {
		content: '';
		height: 1px;
		width: 100%;
		position: absolute;
		top: 0;
	}
}

.header-details-small {
	width: 100%;
	height: 50px;
	padding: 13px 0 12px 0;
	font-size: 18px;

	svg {
		color: $skyblue;
		font-size: 18px;
	}

	span {
		font-size: 18px;

		font-style: normal;
		font-weight: 600;
		line-height: 25px;
		margin-left: 10px;
	}
}
