td,
th {
	height: 25px;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
	page-break-inside: auto;
	width: 100%;
}

table th {
	vertical-align: middle;
}

tr {
	page-break-inside: avoid;
	page-break-after: auto;
}

thead {
	display: table-header-group;
}

tfoot {
	display: table-footer-group;
}

.center {
	text-align: center;
}

.gray {
	color: #808080;
}

.orange-bg {
	background-color: #ffad77;
}

.green-bg {
	background-color: #92d050;
}

.orange-light-bg {
	background-color: #fcd5b4;
}

.green-dark-bg {
	background-color: #00b050;
}

.white-bg {
	background-color: #fff;
}

.section-bg {
	background-color: #c1c1c1;
}

.vertical-top {
	vertical-align: top;
}

.signature {
	text-align: center;
}

.signature > svg {
	position: absolute;
	top: -10px;
}

.left {
	float: left;
}
.right {
	float: right;
}

.pdf-title {
	height: 40px;
	font-size: 14px;
	text-transform: uppercase;
	font-weight: 600;
	padding: 12px;
}
.pdf > span {
	font-style: normal;
	font-weight: 600;
	font-size: 12px;
	line-height: 16px;
}

.pdf-spacing {
	height: 35px;
}

table {
	border-collapse: collapse;
	text-align: left;
	table-layout: fixed;
}

table,
td,
th {
	font-size: 11px;
}

td > span,
th > span {
	padding: 5px 8px 4px 8px;
	line-height: 15px;
}

table > td {
	background-color: #fff;
}

th {
	font-weight: 600;
}

.plain-image {
	height: 500px;
	text-align: center;
}

.image-container {
	height: 550px;
	text-align: center;
}

.image-container.return {
	height: 250px;
}
.image-container.return > td > .plain-image {
	height: 250px;
}

.observations {
	height: 33px;
}

.blank {
	min-height: 60px;
	vertical-align: inherit;
}

.table-well {
	height: 200px;
}

.well {
	width: 150px;
}

.green-light-bg {
	background-color: #cfeecc;
}

tr.white-bg,
tr.white-bg,
td.white-bg {
	max-height: 35px;
	overflow: hidden;
}

.stock-identifier {
	height: 35px;
}

.stock-identifier > span {
	font-style: normal;
	font-weight: normal;
	font-size: 11px;
	line-height: 15px;
}

.identification-request,
.signature-section,
.extension-section,
.return-section {
	margin-bottom: 10px;
}

.description-request {
	padding: 8px;
	border-top: 1px solid #c1c1c1;
}

.description-request > span {
	font-style: normal;
	font-weight: normal;
	font-size: 11px;
	line-height: 15px;
	margin-bottom: 6px;
}

.description-request > .description {
	font-style: normal;
	font-weight: normal;
	font-size: 11px;
	line-height: 15px;
	min-height: 45px;
}

.section-border-rouded {
	border: 1px solid #c1c1c1;
	border-radius: 3px;
}

.section-border-rouded > tr {
	border: none;
}

.section-border {
	border: 1px solid #c1c1c1;
}

.well {
	font-style: normal;
	font-weight: normal;
	font-size: 11px;
	line-height: 15px;
	width: 190px;
	max-height: 300px;
	float: left;
}

.well > div {
	float: left;
	background: #ffffff;
	border: 1px solid #c1c1c1;
	padding: 2px 8px 3px 8px;
	width: 50%;
}

/* TABLE */
.table-header {
	height: 25px;
}
.table-sub-header,
.table-row {
	background-color: #fff;
	height: 35px;
}

.table-col {
	float: left;
}

.table-col {
	padding: 10px 8px 9px 8px;
}

.w-18 {
	width: 18%;
}
.w-46 {
	width: 46%;
}
.w-40 {
	width: 40%;
}
.w-60 {
	width: 60%;
}
.w-20 {
	width: 20%;
}

.non-table-fixed {
	table-layout: auto;
}

table > tbody > tr:last-child > td {
	border-bottom: none;
}

table > tbody > tr > td:last-child,
table > thead > tr > td:last-child {
	border-right: none;
}

table > tbody > tr > td:first-child,
table > thead > tr > td:first-child {
	border-left: none;
}

table > tbody > tr:last-child > td:first-child {
	border-bottom-left-radius: 3px;
}

table > tbody > tr:last-child > td:last-child {
	border-bottom-right-radius: 3px;
}

.reason {
	height: 91px;
	vertical-align: baseline;
}

.section-container {
	background: #f8f8f8;
	border: 1px solid #c1c1c1;
	box-sizing: border-box;
	border-radius: 3px;
	padding: 0 10px 10px 10px;
}
