html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

ol,
ul {
	list-style: none;
}

*,
*:before,
*:after {
	box-sizing: border-box;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-rendering: optimizeLegibility;
}

table,
th,
td {
	vertical-align: middle;
	box-sizing: border-box;
	margin: 0;
}

html,
body {
	font-family: 'Open Sans', sans-serif;
	margin: 0;
	padding: 0;
	color: #000;
	font-size: 11px;
	line-height: normal;
	background-color: #fff;
	width: 100%;
	height: 100%;
	padding: 20px;
	min-width: 800px;
}

.pdf-header {
	padding: 4px 0;
}
.pdf-header > .left > p {
	color: #4a4a4a;
	opacity: 0.5;
}

.title {
	font-weight: 600;
	font-size: 12px;
	opacity: 1 !important;
}
.subtitle {
	opacity: 1 !important;
}
.pdf-header {
	font-size: 11px;
}

.pdf-header > .right > img {
	width: 50px;
	height: 18px;
}
