$easeInCubic     :  cubic-bezier(0.550, 0.055, 0.675, 0.190);
$easeOutCubic    :  cubic-bezier(0.215, 0.610, 0.355, 1.000);
$easeInOutCubic  :  cubic-bezier(0.645, 0.045, 0.355, 1.000);

// Circ
$easeInCirc      :  cubic-bezier(0.600, 0.040, 0.980, 0.335);
$easeOutCirc     :  cubic-bezier(0.075, 0.820, 0.165, 1.000);
$easeInOutCirc   :  cubic-bezier(0.785, 0.135, 0.150, 0.860);

// Expo
$easeInExpo      :  cubic-bezier(0.950, 0.050, 0.795, 0.035);
$easeOutExpo     :  cubic-bezier(0.190, 1.000, 0.220, 1.000);
$easeInOutExpo   :  cubic-bezier(1.000, 0.000, 0.000, 1.000);

// Quad
$easeInQuad      :  cubic-bezier(0.550, 0.085, 0.680, 0.530);
$easeOutQuad     :  cubic-bezier(0.250, 0.460, 0.450, 0.940);
$easeInOutQuad   :  cubic-bezier(0.455, 0.030, 0.515, 0.955);

// Quart
$easeInQuart     :  cubic-bezier(0.895, 0.030, 0.685, 0.220);
$easeOutQuart    :  cubic-bezier(0.165, 0.840, 0.440, 1.000);
$easeInOutQuart  :  cubic-bezier(0.770, 0.000, 0.175, 1.000);

// Quint
$easeInQuint     :  cubic-bezier(0.755, 0.050, 0.855, 0.060);
$easeOutQuint    :  cubic-bezier(0.230, 1.000, 0.320, 1.000);
$easeInOutQuint  :  cubic-bezier(0.860, 0.000, 0.070, 1.000);

// Sine
$easeInSine      :  cubic-bezier(0.470, 0.000, 0.745, 0.715);
$easeOutSine     :  cubic-bezier(0.390, 0.575, 0.565, 1.000);
$easeInOutSine   :  cubic-bezier(0.445, 0.050, 0.550, 0.950);

// Back
$easeInBack      :  cubic-bezier(0.600, -0.280, 0.735, 0.045);
$easeOutBack     :  cubic-bezier(0.175,  0.885, 0.320, 1.275);
$easeInOutBack   :  cubic-bezier(0.680, -0.550, 0.265, 1.550);

// Colors

$red: #ff6060;
$orange: #ffb116;
$greenish: #16f2be;
$green: #46bd8d;
$skyblue: #05abe8;
$blue: #4980ac;
$white: #ffffff;
$backgroundgray: #f7faff;
$softgray1: #f0f7ff;
$softgray2: #e4f0ff;
$softgray3: #becfe7;
$gray1: #424851;
$gray1-alpha2: #4248514d;
$gray2: #2d2f35;
$gray3: #272a30;
$darkgray1: #25272d;
$darkgray2: #24262d;
$bluishgray: #343941;
$black: #000;

$fontFamily: 'Open Sans', -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
	Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
$codeFontFamily: 'Cascadia Code PL', 'Consolas', 'Menlo', sans-serif;
$transition: 0.2s all $easeOutSine;

$variables: (
	font-size: 14px,
	font-color: $softgray2,
	secondary-color: $softgray3,
	page-background: #202126,
	active-border: $skyblue,
	field-background: $bluishgray, // Field
	field-border: $gray1,
	field-secondary-background: $gray1,
	field-darken-background:$gray2,
	button-default: $gray1, // Button
	button-primary: $skyblue,
	button-border: $gray1,
	button-color: $white,
	button-disabled: $bluishgray,
	check-background: $gray2, // CheckField
	check-border: $gray1,
	dropdown-background: $bluishgray, // Dropdown
	dropdown-border: $gray1,
	dropdown-hover: $gray3,
	popover-background: $gray1, // Popover
	popover-border: $gray2,
	inner-section-bg: $gray3,
	dark-section-bg: $darkgray2,
	dark-section-border: $darkgray2,
	section-background: $bluishgray, // Section
	section-border: $bluishgray,
	section-content-background: $gray1,
	switch-border: $gray1, // Switch
	switch-default: $bluishgray,
	switch-secondary: $gray1,
	card-background: $gray1, // Card
	menu-lvl1: $darkgray2,
	menu-lvl2: $bluishgray,
	menu-lvl3: $gray1,
	menu-border: rgba($color: $softgray3, $alpha: 0.2)
);

$lightVariables: (
	font-color: $gray1,
	secondary-color: $blue,
	page-background: $backgroundgray,
	field-background: $white, // Field
	field-border: $softgray3,
	field-secondary-background: $white,
	field-darken-background:$backgroundgray,
	button-default: $white, // Button
	button-color: $gray2,
	button-border: $softgray3,
	check-background: $white, // CheckField
	check-border: $softgray3,
	dropdown-background: $white, // Dropdown
	dropdown-hover: $softgray2,
	dropdown-border: $softgray3,
	popover-background: $white, // Popover
	popover-border: $softgray2,
	section-background: $white, // Section
	section-border: $softgray3,
	inner-section-bg: $backgroundgray,
	dark-section-bg: $white,
	dark-section-border: $softgray3,
	section-content-background: $backgroundgray,
	switch-border: $softgray3, // Switch
	switch-default: $white,
	switch-secondary: $backgroundgray,
	card-background: $white, // Card
	menu-lvl1: $white,
	menu-lvl2: rgba($color: $backgroundgray, $alpha: 0.6),
	menu-lvl3: $softgray2,
	menu-border: $softgray3,
);
